import * as React from "react";
import { Box, Toolbar, useMediaQuery, IconButton } from "@mui/material";
import { styled } from "@mui/material/styles";
import { Menu as MenuIcon, Close as CloseIcon } from "@mui/icons-material";
import { Link as RouterLink } from "gatsby";
import { useLocation } from "@reach/router";
import CommonButton from "../common/CommonButton/CommonButton";
import BrandLogo from "../common/BrandLogo";
import { pagesLinks as links } from "../../globals";
import { useState } from "react";

const Navigation = styled(Toolbar)(({ theme }) => ({}));

export default function NavBar() {
  const location = useLocation();
  const smallScreen = useMediaQuery((theme) => theme.breakpoints.down("sm"));
  const [showMenu, setShowMenu] = useState(false);

  const handleMenuClick = () => {
    setShowMenu(!showMenu);
  };

  return (
    <Box sx={{ display: "flex", justifyContent: "center", p: 2 }}>
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          mr: "auto"
        }}
      >
        <RouterLink to="/">
          <BrandLogo />
        </RouterLink>
      </Box>

      {smallScreen ? (
        <IconButton onClick={handleMenuClick}>
          <MenuIcon />
        </IconButton>
      ) : (
        <Box sx={{ mx: "auto" }}>
          <Navigation>
            {links.map((link) => (
              <CommonButton
                key={link.path}
                component={RouterLink}
                to={link.path}
                sx={{
                  textTransform: "unset",
                  fontWeight: "bold",
                  color:
                    link.path === "/" ? location.pathname === "/" ? "primary.main" : "inherit" :
                      location.pathname.startsWith(link.path) && (link.path !== "/")
                        ? "primary.main"
                        : "inherit",
                  "&:hover": {
                    color: "primary.main"
                  }
                }}
              >
                {link.label}
              </CommonButton>
            ))}
            <Box sx={{ flexGrow: 1 }} />
          </Navigation>
        </Box>
      )}

      {/* Render menu items as a drawer or a dropdown */}
      {smallScreen && (
        <Box
          sx={{
            position: "fixed",
            top: 0,
            left: 0,
            pt: 4,
            width: "100vw",
            height: "100vh",
            display: showMenu ? "block" : "none",
            backgroundColor: "#fff",
            zIndex: 1000,
            textAlign: "center"
          }}
        >
          {links.map((link) => (
            <CommonButton
              key={link.path}
              component={RouterLink}
              to={link.path}
              sx={{
                textTransform: "unset",
                fontWeight: "bold",
                color:
                  location.pathname === link.path ? "primary.main" : "inherit",
                "&:hover": {
                  color: "primary.main"
                }
              }}
            >
              {link.label}
            </CommonButton>
          ))}
          <IconButton
            sx={{
              // width: "100%",
              textTransform: "unset",
              fontWeight: "bold",
              color: "inherit",
              "&:hover": {
                color: "primary.main"
              }
            }}
            onClick={() => setShowMenu(false)}
          >
            <CloseIcon />
          </IconButton>
        </Box>
      )}
    </Box>
  );
}
